export default {
	data() {
		return {
			nav: [{
				id: 0,
				name: '全部'
			}, {
				id: 1,
				name: '待付款'
			}, {
				id: 2,
				name: '待发货'
			}, {
				id: 3,
				name: '待收货'
			}, {
				id: 4,
				name: '待评价'
			}],
			current: 0
		};
	},
	methods: {
		select(num) {
			this.current = num
		},
		change(e) {
			this.current = e.detail.current
		}
	},
	onShow() {},
	onLoad(options) {
		this.current = options.num
	}
};
